import { map, lerp, wheel, mouse } from '../utils'

class VariableText {
  constructor (el, initialSettings) {
    this.$el = el instanceof HTMLElement ? el : document.querySelector(el)
    this.$settings = initialSettings
    this.apply()
  }

  set width (val) {
    this.$settings.width = Math.min(1000, Math.max(100, val))
    this.apply()
  }
  
  set weight (val) {
    this.$settings.weight = Math.min(700, Math.max(100, val))
    this.apply()
  }

  set slant (val) {
    this.$settings.slant = Math.min(15, Math.max(-15, val))
    this.apply()
  }

  get width () { return this.$settings.width }
  get weight () { return this.$settings.weight }
  get slant () { return this.$settings.slant }

  apply () {
    this.$el.style.fontVariationSettings = `'wdth' ${this.$settings.width}, 'wght' ${this.$settings.weight}, 'slnt' ${this.$settings.slant}`
  }
}

const vtext = new VariableText('h1.text', {
  width: 100,
  weight: 100,
  slant: 0
})

requestAnimationFrame(update)
function update () {
  requestAnimationFrame(update)

  const slant = map(mouse.y, 0, 1, -15, 15)
  vtext.slant = lerp(vtext.slant, slant, 0.15)

  const weight = map(Math.abs(mouse.x), 0, 1, 100, 700)
  vtext.weight = lerp(vtext.weight, weight, 0.15)

  // const rawWidth = wheel.y - 
  const width = map(wheel.deltaY, -5, 5, 100, 1000)
  vtext.width = lerp(vtext.width, width, 0.15)
}
